.b-team {
    &__layout {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        padding: 4rem 20px;
        @include respond-to(992) {
            flex-direction: row;
            max-width: 1140px;
            margin: 0 auto;
            position: relative;
            padding: 3rem 0 8rem;
            justify-content: space-between;
        }
    }
    
    &__footer { 
        position: relative;
        padding: 4rem 20px;
        text-align: center;
        @include respond-to(1200) {
            padding: 0;
            margin-bottom: -60px;
            margin-top: -30px;
        }
    }
    
    &__load-more {
        text-align: center;   
        z-index: 99;
        padding: 2rem 0;
    }
    
    .b-team__footer-box {
        @include respond-to(1200) {
            position: relative;
            top: -160px;
            left: 15px;
            max-width: 310px;
            text-align: left;
        }
    }
    
    .b-team__footer-title {
        color: $c-tautara;
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 0.6rem;
    }
    
    .b-team__footer-text {
        color: $c-tautara;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 1.8rem;
    }
    
    .b-team__footer-link {
        
    }
    
    &__person {
        flex: 0 0 340px;
        box-shadow: $shadow-2;
        border-radius: 6px;
        background-color: $c-white;
        text-align: center;
        padding-bottom: 1.1rem;
        max-width: 340px;
        margin: 0 auto 3rem;
        z-index: 99;
        
        @include respond-to(992) {
            flex: 0 0 300px;
            max-width: none;
            margin-bottom: 60px;
        }
        @include respond-to(1200) {
            flex: 0 0 340px;
        }
        
        &:nth-child(3n+2) {
            @include respond-to(992) {
                position: relative;
                top: 3rem;
            }
        }
        
        &:nth-child(3n+3) {
            @include respond-to(992) {
                position: relative;
                top: 7.3rem;
            }
        }    
    }
    .b-team__person-img {
        margin: 0 auto 1rem;
    }
    
    .b-team__person-name {
        color: $c-tautara;
        font-size: 16px;
        font-weight: 700;
        line-height: 26px;
        margin-bottom: 2px;
    }
    
    .b-team__person-role {
        color: $c-tautara;
        font-family: $roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;   
        margin-bottom: 3px;
    }
    
    .b-team__person-contact {
        color: $c-green;
        font-family: Roboto;
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
    }
}