.b-request-quote {
	
	@include respond-to(992) {
		position: relative;
		top: -22px;
		left: 16px;
	}
	
	&__layout {
		
		@include respond-to(992) {
			display: grid;
			grid-template-columns: 1.8fr 405px;
		}
	}
	
	&__left {
		background-color: $c-white;
		position: relative;
		padding: 2.5rem 2rem 3rem;
		
		@include respond-to(992) {
			border-radius: 13px;
			box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
			padding: 2.5rem 2.8rem;
		}
	}
	
	&__right {
		background-color: $c-white;
		@include respond-to(992) {
			background-color: transparent;
		}
	}
	
	&__title,
	&__sub-title,
	&__intro-text {
		text-align: center;
	}
	
	&__title {
		color: $c-green;
		letter-spacing: 0.06rem;
		@include respond-to(1200) {
			margin-bottom: 1.2rem;
		}
	}
	
	&__sub-title {
		font-size: 14px;
		font-weight: 700;
		margin-bottom: 1rem;
		
		@include respond-to(992) {
			margin-bottom: 0;
		}
	}
	
	&__intro-text {
		font-size: 14px;
		font-weight: 400;
		max-width: 430px;
		margin: 0 auto;
		@include respond-to(1200) {
			line-height: 21px;
		}
	}
	
	.m-form__body {
		margin-top: 0.6rem;
		
		@include respond-to(992) {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 0 35px;
		}
	}
	
	.m-form__fieldset {
		margin-bottom: 0;
		
		@include respond-to(992) {
			margin-bottom: -1.3rem;
		}
		
		label {
			left: 16px;
			letter-spacing: -0.035rem;
			
			&.checkbox {
				left: 0;
			}
		}
	}
	
	&__location {
		text-align: center;
		padding: 0 1.3rem 3rem;
		display: flex;
		flex-direction: column;
		
		@include respond-to(992) {
			display: block;
			padding-left: 62px;
			text-align: left;
			padding-bottom: 0;
		}
		
		hr {
			margin: 3.3rem auto 0.4rem;
			@include respond-to(992) {
				margin: 5.3rem 0 0.4rem;
			}
		}
	}
	
	&__location-img {
		padding-top: 6rem;
		margin: 0 auto;
		order: 2;
		@include respond-to(992) {
			max-width: 362px;
			position: relative;
			top: 23px;
			padding-top: 0;
		}
	}
	
	&__location-title {
		font-size: 16px;
		font-weight: 700;
		line-height: 26px;
		text-transform: uppercase;
	}
	
	&__location-address, 
	&__location-link {
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		color: $c-tautara;
		
		p {
			font-size: 14px;
			font-weight: 700;
			line-height: 20px;
			margin-bottom: .6rem;
		}
	}
	
	&__location-address {
		@include respond-to(992) {
			max-width: 240px;
		}
	}
	
	.b-request-quote__form {
		
		.l-btn {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: -18px;
		}
	}
}
