.b-partnerships {
    background-color: $gainsboro;
    overflow: hidden;
    position: relative;
    padding: 3rem 2rem 3rem;
    @include respond-to(1200) {
        top: -62px;
        padding: 0 0 1.2rem;
    }
}

.b-partnerships__layout {
    max-width: 997px;
    margin: 0 auto;
    text-align: center;
    grid-gap: 40px;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "intro intro"
    "tm-model teach-us"
    "fixed-projects create-your";

    @include respond-to(768) {
        grid-template-columns: 1fr 1fr;
        display: grid;
        grid-gap: 40px;
        text-align: center;
    }

    @include respond-to(1200) {
        padding: 1rem 0;
        background: url(../../img/partnerships-hexagone.svg) no-repeat center;
        position: relative;
        top: -215px;
        left: -30px;
        text-align: left;
        grid-gap: 60px;
        background-size: 930px 932px;
        margin-bottom: -100px;
        grid-template-areas: 
        "intro tm-model"
        "teach-us fixed-projects"
        "create-your .";
    }
    
    .h2 {
        margin-bottom: 1rem;
        @include respond-to(992) {
            margin-bottom: 0;
        }
    }
    
    .m-box-hexagone {
        margin-bottom: 3rem;
        @include respond-to(1200) {
            padding: 20px;
            margin-bottom: 0;
        }
    }
    
    .m-box-hexagone__body {
        margin: 0 auto;
        @include respond-to(1200) {
            padding-right: 30px;
            margin-left: 0;
        }
    }
    
    .m-box-hexagone {
        &:nth-child(1) {
            grid-area: intro;
            @include respond-to(992) {
                max-width: 50%;
                margin: 0 auto 3rem;
            }
            @include respond-to(1200) {
                left: -46px;
                max-width: 100%;
                min-width: 480px;
                position: relative;
                top: 78px;
            }
            p {
                font-size: 15px;
                @include respond-to(992) {
                    font-size: 14px;
                    line-height: 20px;
                }
                @include respond-to(1200) {
                    max-width: 340px;
                    position: relative;
                    top: 14px;
                }
            }
        }
        
        &:nth-child(2) {
            grid-area: tm-model;
            @include respond-to(1200) {
                position: relative;
                top: 138px;
                left: 35px;
            }
        }
        
        &:nth-child(3) {
            @include respond-to(1200) {
                position: relative;
                left: 189px;
                top: -11px;
            }
        }
        
        &:nth-child(4) {
            grid-area: fixed-projects;
            @include respond-to(1200) {
                position: relative;
                top: 232px;
                left: 35px;
            }
        }
        
        &:nth-child(5) {
            grid-area: create-your;
            @include respond-to(1200) {
                position: relative;
                top: 72px;
                left: 189px;
            }
        }
        &:nth-last-child(1) {
            margin-bottom: 0;
        }
    }
}
