.b-blog-post {
    &__layout {
        
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
        
        @include respond-to(1200) {
            max-width: 1000px;
            position: relative;
            top: -200px;
            box-shadow: $shadow-2;
        }
        
        & > div:first-child {
            border-top-left-radius: 6px;
            border-top-right-radius: 6px;
        }
        
        & > div:last-child {
            border-bottom-left-radius: 6px;
            border-bottom-right-radius: 6px;
        }
        
        & > .l-clearfix {
            
            .col-half {
                
                &:nth-child(odd) {
                    @include respond-to(1200) {
                        padding-right: 30px;
                    }
                }
                
                &:nth-child(even) {
                    padding-top: 1.2rem;
                    @include respond-to(1200) {
                        padding-top: 0;
                        padding-left: 30px;
                    }
                }
            }
        }
        
        hr {
            margin: 1.6rem 0;
        }
        
        h3 {
            color: $c-tautara;
            font-size: 20px;
            font-weight: 700;
            line-height: 22px;
        }
        h4 {
            color: $c-tautara;
            font-weight: 700;
            font-size: 16px;
            line-height: 22px;
            margin-bottom: 15px;
            position: relative;
            padding-left: 20px;
            span {
                color: $c-yellow;
                font-size: 18px;
                position: absolute;
                left: 0;
            }
        }
        p {
            color: $c-tautara;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 1.2rem;
            b {
                font-weight: 500;
            }
            & + ol {
                position: relative;
                top: -1.2rem;
            }
        }
        
        a {
            color: $c-green;
            font-weight: 500;
        }
        ol {
            list-style: decimal;
            padding-left: 30px;
            li {
                color: $c-tautara;
                font-family: $roboto;
                font-size: 14px;
                font-weight: 400;
                line-height: 20px;
            }
        }
    }
    
    &__header {
        text-align: center;
        background: $c-white;
        padding: 0 1.3rem 1.3rem;
        
        @include respond-to(1200) {
            padding: 93px 7.6rem 3.3rem;
        }
        
        hr {
            margin: 1rem auto 1.3rem;
        }
        
        h2 {
            letter-spacing: -0.05rem;
            font-range: 320px 1920px;
            font-size: responsive 20px 36px;
            margin: 1rem 0;
            @include respond-to(1200) {
                line-height: 46px;
            }
        }
        
        p {
            font-size: 14px;
            line-height: 20px;
            padding: 0 1.5rem;
            
            @include respond-to(1200) {
                padding: 0 70px;
            }
        }
    }
    
    &__meta {
        font-size: 14px;
        margin-top: 3px;
        margin-bottom: 27px;
    }
    
    .col-full, 
    .col-half {
        float: left;
    }
    
    .col-full {
        width: 100%;
        padding: 0 50px;
    }
    
    .col-half {
        @include respond-to(1200) {
            width: 50%;
        }
    }
    
    .content-split {
        max-width: 100%;
        width: 100%;
        margin: 2.6rem auto;
        height: 1px;
        background-color: rgba(151, 151, 151, 0.2);
    }
    &__content-space {
        padding: 3rem 0;
    }
}

.b-blog-post__related {
    margin-top: -45px;
    padding-bottom: 4rem;
    &-layout {
        @include respond-to(992) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            margin: 0 auto;
            max-width: 1200px;
        }
        @include respond-to(1200) {
            padding-top: 0;
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 20px 0px;
        }
    }
    .l-btn {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 26px;
    }
}