.b-our-speakers {
    background-color: $gainsboro;
    padding: 3rem 0;
    
    @include respond-to(1200) {
        padding: 0 0 6rem;
    }
    
    &__layout,
    &__intro {
        padding: 0 2rem;
        @include respond-to(1200) {
            padding: 0;
            position: relative;
            left: 15px;
        }
    }
    
    &__layout {
        max-width: 1200px;
        margin: 3rem auto 0;
        
        @include respond-to(992) {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
        
        @include respond-to(1200) {
            grid-template-columns: 1fr 1fr 1fr;
            margin: 60px auto 0;
        }
    }
    
    &__intro {
        text-align: center;
        @include respond-to(1200) {
            text-align: left;
            margin-top: -76px;
            max-width: 360px;
        }
    
        h2 {
            margin-bottom: 1rem;
            @include respond-to(1200) {
                margin-bottom: 11px;
            }
        }
    }
    
    &__body {
        color: $c-tautara;
        font-family: $roboto;
        font-size: 14px;
        margin-bottom: 2rem;
        @include respond-to(1200) {
            line-height: 20px;
            letter-spacing: 0.003rem;
            margin-bottom: 28px;
        }
    }
    .m-article {
        &:nth-last-child(1) {
            margin-bottom: 0;
            @include respond-to(992) {
                margin: 0 auto 2.5rem;
            }
        }
    }
}
