.b-promotion {
	padding: 3rem 2rem;
	text-align: center;
	
	h2 {
		font-size: 24px;
		@include respond-to(mobile) {
			margin-bottom: 1rem;
		}
	}
	
	hr {
		margin: 0 auto 1rem;
		@include respond-to(768) {
			margin: 1rem auto;
		}
	}
	.b-promotion__text {
		max-width: 500px;
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		margin: 0 auto 1.2rem;
	}
	input {
		box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
		border-radius: 13px;
		border: 1px solid rgba(151, 151, 151, 0.2);
		background-color: #ffffff;
		padding-right: 146px;
	}
	.l-btn {
		width: 170px;
		padding: 8px;
	}
	.m-form--newsletter {
		margin-top: 31px;
	}
	.m-form__fieldset {
		position: relative;
		max-width: 430px;
		margin: 0 auto;
	}
	.m-form__btn {
		border: none;
		position: absolute;
		top: 0;
		right: 0;
		width: 142px;
		height: 54px;
		box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
		border-radius: 13px;
		background-color: $c-green;
	}
	
	&--home {
		padding: 3rem 2rem;
		@include respond-to(768) {
			padding: 2.7rem 0 4.7rem;
		}
		p {
			margin-bottom: 1.5rem;
			color: $c-tautara;
			font-size: 14px;
			@include respond-to(768) {
				line-height: 20px;
			}
		}
	}
	&--careers {
		padding: 2.5rem 0 5rem;
		hr {
			margin: 1rem auto 0.2rem;
		}
		.b-promotion__title {
			font-size: 30px;
		}
		.b-promotion__sub-title {
			font-size: 18px;
			font-weight: 500;
			line-height: 20px;
			margin-bottom: 22px;
		}
		.b-promotion__text {
			margin-bottom: 2rem;
		}
		.l-btn {
			width: 145px;
			padding: 3px;
		}
	}
}
