.b-built-by {
	padding: 3rem 2rem 3rem;
	@include respond-to(1200) {
		padding: 3rem 0 2.2rem;
	}
	
	&__layout {
		
		@include respond-to(992) {
			margin-bottom: 3rem;
			max-width: 1200px;
			margin: 0 auto;
			display: grid;
			grid-template-areas: 
			"header header"
			"article article";
		}
		
		@include respond-to(1200) {
			width: 100%;
			display: grid;
			grid-template-areas: 
			"header header header"
			"article article article";
			grid-template-columns: 1fr 1fr 1fr;
			position: relative;
			left: 15px;
		}
	}
	
	&__header {
		text-align: center;
		grid-area: header;
		margin-bottom: 1.5rem;
		@include respond-to(992) {
			margin-bottom: 2.6rem;
		}
	}
	
	h2 {
		letter-spacing: $letter-spacing--big;
	}
	
	.b-built-by__subtitle {
		color: $c-tautara;
		font-family: $roboto;
		font-size: 14px;
		padding: 0 2rem;
		@include respond-to(992) {
			margin-top: -3px;
			margin-bottom: 5px;
			padding: 0 1rem;
		}
	}
	
	.m-article {
		@include respond-to(992) {
			margin-bottom: 3rem;
		}

		&:nth-last-child(1) {
			margin-bottom: 0;
			@include respond-to(992) {
				margin-bottom: 3rem;
			}
		}
	}
}
