.b-help {
    padding: 3rem 2rem;
    @include respond-to(992) {
        padding: 2.2rem;
    }
    @include respond-to(1400) {
        padding: 2.8rem 0 1.7rem;
    }
    &--about-us {
        @include respond-to(1400) {
            padding: 4.1rem 1.7rem;
        }
        .m-box--custom {
            @include respond-to(1200) {
                max-height: none;
            }
        }
        .m-box--marketing {
            @include respond-to(1200) {
                position: relative;
                top: 137px;
            }
        }
    }
    .b-help__intro-title {
        letter-spacing: -.05rem;
        font-size: 28px;
        @include respond-to(1200) {
            letter-spacing: .01rem;
            line-height: 42px;
        }
    }
}

.b-help__layout {
    display: grid;
    grid-gap: 60px;
    @include respond-to(768) {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
        "a a"
        "mobile marketing"; 
    }
    
    @include respond-to(1200) {
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-areas: 
        "a a a"
        ". mobile marketing";
        position: relative;
        left: 16px;
    }
    
    .b-help__intro {
        @include respond-to(768) {
            grid-area: a;
        }
        @include respond-to(768) {
            grid-area: initial;
        }
    }
    
    .m-box {
        @include respond-to(1200) {
            &:nth-child(4) {
                grid-area: mobile;
            }
            &:nth-child(5) {
                grid-area: marketing;
            }
        }
        &:nth-child(3) {
            @include respond-to(1200) {
                position: relative;
                top: 136px;
                max-height: 255px;
            }
        }
        &:nth-child(5) {
            @include respond-to(1200) {
                position: relative;
                top: 63px;
            }
        }
    }
}

.b-help__intro-title {
    @include respond-to(1200) {
        margin-top: 10px;
    }
}

.b-help__intro-body {
    font-size: 14px;
    margin-top: 17px;
    margin-bottom: 23px;
    @include respond-to(1200) {
        line-height: 19px;
    }
}
