.b-work {
    padding: 6.5rem 20px;
    @include respond-to(1200) {
        padding: 0.8rem 0 8.5rem;
    }
    
    &__title {
        margin-left: 1rem;    
        line-height: 45px;
        max-width: 360px;
        margin-bottom: 6.7rem;
    }
    
    &__layout {
        @include respond-to(992) {
            display: grid;
            grid-gap: 30px;
            grid-template-columns: 1fr 1fr;
        }
        @include respond-to(1200) {
            display: grid;
            grid-gap: 60px;
            grid-template-columns: 1fr 1fr 1fr;
            position: relative;
            left: 15px;
        }
    }
}
